import React from 'react'
import { Button, Card, CardBody, CardHeader, Flex, Heading, Text } from '@pancakeswap/uikit'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import useTheme from 'hooks/useTheme'
import { useTranslation } from 'contexts/Localization'

const Hero: React.FC = () => {
  const { t } = useTranslation()
  const { theme } = useTheme()
  const { account } = useWeb3React()

  return (
    <section className="invest-top">
      <div className="container">
        <div className="row">
          <div className="col-md-3">
            <img src="images/jam.png" className="img-fluid" alt="" />
          </div>
          <div className="col-md-6">
            <h3 className="font-700 mb-10 ">Investments with compounding yield</h3>
            <p className="text-16">Auto-compounding vaults that compound at an optimal rat</p>
            <div className="top-search mt-24">
              <input type="text" className="form-control" placeholder="Search by name or symbol"/>
              <span className="srch"><img src="images/search.png" alt="" /></span>
            </div>
          </div>
          <div className="col-md-3">
            <img src="images/knife.png" className="img-fluid" alt="" />
          </div>
        </div>
      </div>
    </section>
  )
}

export default Hero
